@use './root';
@use './display';
@use './grid';
@use './colors';
@use './forms';
@use './variables' as *;
@use './typography';
@use './margin';
@use './flexbox';

.miq-container {
    max-width: map-get($map: $breakpoints, $key: md);

    &.center {
        margin: 0 auto;
    }

    &-fluid {
        margin: 0;
        max-width: 100%;
    }
}

.round {
    border-radius: 50%;
}

.rounded {
    border-radius: var(--border-radius, 6px);
}

.text-muted {
    color: var(--gray-600);
}

// BORDERS

.border-1 {
    border: 1px solid var(--border-color);
}

.border-bottom {
    border-bottom: 1px solid var(--border-color);
}

.border-0 {
    border: 0;
}

// WIDTH

.w-100 {
    width: 100%;
}

// COLOR

.bg-white {
    background-color: white;
}

// RESET

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

body {
    // margin: 0 0 var(--header-height) 0;
    font-family: var(--ff);
    font-size: var(--fs);
    // background-color: var(--body-color);
    // color: var(--text-color);
}

h1,
h2,
h3,
h4,
h5,
h6 {
    // color: var(--title-color);
    // font-weight: var(--font-semi-bold);
    font-family: var(--ff-heading);
}

h1 {
    font-size: var(--fs-xxl);
}
h2 {
    font-size: var(--fs-xl);
}
h3 {
    font-size: var(--fs-lg);
}
h4 {
    font-size: var(--fs-md);
}
h5 {
    font-size: var(--fs-sm);
}
h6 {
    font-size: var(--fs-sx);
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
    height: auto;
}
